* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 16pt;
}
@media (max-width: 480px) {
  html {
    font-size: 12pt;
  }
}
body {
  background-color: #F8F8FB;
  font-family: "Lab Grotesque", serif;
  overflow: hidden;
}
.appWrapper {
  display: flex;
  justify-content: space-around;
  overflow-y: scroll;
  height: 100vh;
}
.appWrapper::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
.appWrapper::-webkit-scrollbar-thumb:hover {
  background-color: #adadad;
}
.appWrapper::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #C4C4C4;
}
.appWrapper:before {
  content: "";
  min-width: 25px;
}
.appWrapper::after {
  content: "";
  min-width: 25px;
}
.appContainer {
  flex-grow: 1;
  max-width: 800px;
  height: 100%;
}
.shoppingList {
  width: 100%;
  margin: 20px 0;
  border-radius: 10px;
  padding: 20px;
  box-sizing: border-box;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
}
.shoppingListHeader {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 20px 0;
  margin-bottom: 20px;
}
.shoppingListHeader > h2 {
  flex-grow: 1;
  font-size: 1.125rem;
}
.shoppingListHeader > svg,
.shoppingListHeader > a {
  flex-grow: 0;
  height: 1.125rem;
  cursor: pointer;
}
.itemContentLeftPart {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  border-radius: 10px;
  border: 1px solid #d9d9d9;
  background-color: #ffffff;
  margin: 20px 0;
  padding: 20px 20px 20px 30px;
  font-size: 1.125rem;
  user-select: none;
  box-sizing: border-box;
  word-break: break-word;
}
.itemContentLeftPart span {
  padding-left: 5px;
  color: #8d8d8d;
  font-weight: 400;
  font-size: 0.75rem;
}
.itemContentLeftPart label {
  font-weight: bolder;
}
.boughtProduct {
  border: 1px solid #999999;
  background-color: #cccccc;
}
.shoppingListItemOptions {
  color: #616161;
  transition: font-size 0.5s, margin-top 0.5s, color 0.5s;
  width: 100%;
}
.openedOptions {
  margin-top: 25px;
  font-size: 0.875rem;
  color: #616161;
}
.closedOptions {
  margin-top: 0;
  font-size: 0;
  color: rgba(255, 255, 255, 0);
  overflow: hidden;
}
.arrowReverse {
  transform: scaleY(-1);
}
.arrow {
  transition: transform 0.5s ease-out;
  padding-left: 8px;
}
.icon {
  height: 1.125rem;
  width: 20px;
}
.icon > path {
  fill: black;
}
